














import { mapState, mapActions } from "vuex";

export default {
  computed: {
      ...mapState(["token"])
  },
  methods:{
    loginOut(){
      this.$store.dispatch('LoginOut').then(() => {
            uni.redirectTo({
              url: '../login/login',
          });
      })
    }
  },
  onLoad() {
		console.log('token===' + this.token)
    if (!this.token) {
      this.Util.Toast.showModal("未登录", "登录后才能继续", res => {
        if (res.confirm) {
          uni.redirectTo({
            url: "../login/login" 
          });
        }
      });
    }
  }
};
